import { Component, OnInit, HostListener  } from '@angular/core';
import { TmdbService } from '../services/tmdb.service';
import { TmdbResponse, Film } from '../interfaces/interfaces';
import { ModalController } from '@ionic/angular';
import { LoginPage } from '../auth/pages/login/login.page';
import { AuthService } from '../auth/auth.service';


const screenWidthBreakPoint: number = 420;

@Component({
  selector: 'app-tab1',
  templateUrl: 'tab1.page.html',
  styleUrls: ['tab1.page.scss']
})
export class Tab1Page implements OnInit {

  logoUrl: string = "../../assets/logo.svg";

  recentFilms: Film[] = [];

  trendingAll: Film[] = [];

  popularFilms: Film[] = [];

  trendingSeries: Film[] = [];

  populartSeries: Film[] = [];

  loginText: string = "";

  screenWidth: number;

  auth: boolean = false;

  constructor(private tmdb: TmdbService,
    private modalController: ModalController,
    private authService: AuthService
    ) {
  }

  ngOnInit(): void {
    this.getRecentFilms();
    this.getPopularFilms();
    this.getTrendings();
    this.getTrendingSeries();
    this.getPopularSeries();
    this.setLoginText();
    this.isAuth();
  }

  isAuth() {
    this.authService.whoAmI().subscribe(res => this.auth = true)
  }


  @HostListener('window:resize', ['$event'])
  setLoginText(event?) {
    this.screenWidth = window.innerWidth;
    if (this.screenWidth > screenWidthBreakPoint){
      this.loginText="Login"
    } else {
      this.loginText=""
    }
  }


  getRecentFilms(): void{
    this.tmdb.getMoviesOfMonth().subscribe( res => {
      this.recentFilms = res.results.filter(this.filmfilter);
    })
  }

  getTrendings(): void{
    this.tmdb.getTrendings().subscribe( res => {
      this.trendingAll = res.results.filter(this.filmfilter);
    })
  }

  getPopularFilms(): void{
    this.tmdb.getPopularFilms().subscribe( res => {
      const tempArr = [...this.popularFilms, ...res.results]
      this.popularFilms = tempArr.filter(this.filmfilter);
    })
  }

  getTrendingSeries(): void{
    this.tmdb.getTrendingSeries().subscribe( res => {
      this.trendingSeries = res.results.filter(this.filmfilter);
    })
  }

  getPopularSeries(): void{
    this.tmdb.getPopularSeries().subscribe( res => {
      this.populartSeries = res.results.filter(this.filmfilter);
    })
  }

  loadMorePopularFilms(): void{
    this.getPopularFilms();
  }

  filmfilter(film: Film): boolean {
    if(film.poster_path){
      return true
    } else {
      return false
    }
  }

  async login() {
    const loginModal = await this.modalController.create({
      component: LoginPage,
    });
    loginModal.onDidDismiss().then(data => {this.isAuth()})
    return await loginModal.present();
  }

}
